___CSS_LOADER_EXPORT___.push([module.id, `*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Poppins", sans-serif;
    font-size: 16px;

}
:root {
    --font-color: #fafafa;
    --green-color: #0bf25c;
}
body {
    color: var(--font-color)
}

.container {
    width: 100%;
    max-width: 100%;
    min-height: 100vh;
    background: linear-gradient(135deg,#6366f1, #2563eb, #1d4ed8, #172554, #4338ca, #1e1b4b);  
    display: flex;
    gap: 20px;
}
.sidebar {
    min-height: 100vh;
    width: 20%;
    background-color: #1e3a8a;
    color: #d1d5db;
    opacity: .9;
    align-self: flex-start;
}
.sidebar h2 {
    display: flex;
    align-items: center;
    margin-top: 20px;
    margin-left: 30px;
    font-size: 1.5rem;
}
.sidebar img {
    width: 40px;
    height: 40px;
    margin-left: 20px;
}
.sidebar h4 {
    display: flex;
    align-items: center;
    padding: 10px;
    width: 100%;
    margin-top: 5px;
    margin-left: 0;
    font-size: 1rem;
    cursor: pointer;
}
.sidebar h4:hover {
    background-color: #182d69;
    opacity: .7;
}
.sidebar h4 img {
    width: 30px;
    height: 30px;
    margin-right:20px;

}
.sidebar .projects {
    margin-top: 40px;
    display: flex;
    align-items: center;
}
.sidebar .projects h4 {
    font-size: 1.25rem;
    margin-top: 0;
}
.sidebar .projects h4 img{
    width: 35px;
    height: 35px;
    margin-right: 15px;
}
.tasks-container {
    background-color: #27272a;
    width: 77.5%;
    min-height: 100vh;
    border-radius: 20px 20px 0 0;
    margin-top: 20px;
}
.tasks-container button  {
    text-align: center;
    cursor: pointer;
    margin-top: 10px;
    margin-left: 10px;
    background-color: #27272a;
    border: none;

}
.tasks-container button img {
    width: 60px;
    height: 60px;
}
.tasks-container.tasks-div {
    margin-top: 0;
    width: 100%;
    min-height: 90%;

}
.cont-div {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 97%;
    margin: 15px 20px;
    padding: 10px 20px;
    border-radius: 4px;
    background-color: #2f2f33;
    /* flex-wrap: wrap; */
    max-width: 100%;
    word-wrap: break-word;
    overflow-wrap: break-word;
}
.cont-div > * {
    flex-grow: 0;
    flex-shrink: 0;
}
.cont-div h4 {
    font-size: 1.5rem;
    color: #e2e8f0;
    margin-left: 30px;
    text-align: center;
    flex-shrink: 0;
    max-width: 100%;
}

.cont-div p {
    font-size: .9rem;
    padding: 5px;
    flex-shrink: 0;
    width: 100%;
}
.cont-div p:nth-of-type(1) {
    white-space: normal;
    overflow: hidden;
    text-overflow: ellipsis;
    line-height: 1.5;
    max-height: 150px; /* Optional: limit height */
    overflow-y: auto; /* Add scroll if content exceeds max-height */
}
.important {
    color: var(--green-color);
    font-style: italic;
    font-size: 1.1rem;
}
.delete-icon {
    width: 20px;
    height: 20px;
    cursor: pointer;
    margin-bottom: -20px;
    margin-left: -20px;
}
.complete-icon {
    width: 20px;
    height: 20px;
    cursor: pointer;
    border: none;
    border-radius: 50%;
    margin-bottom: -30px;
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA;IACI,SAAS;IACT,UAAU;IACV,sBAAsB;IACtB,kCAAkC;IAClC,eAAe;;AAEnB;AACA;IACI,qBAAqB;IACrB,sBAAsB;AAC1B;AACA;IACI;AACJ;;AAEA;IACI,WAAW;IACX,eAAe;IACf,iBAAiB;IACjB,wFAAwF;IACxF,aAAa;IACb,SAAS;AACb;AACA;IACI,iBAAiB;IACjB,UAAU;IACV,yBAAyB;IACzB,cAAc;IACd,WAAW;IACX,sBAAsB;AAC1B;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,gBAAgB;IAChB,iBAAiB;IACjB,iBAAiB;AACrB;AACA;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;AACrB;AACA;IACI,aAAa;IACb,mBAAmB;IACnB,aAAa;IACb,WAAW;IACX,eAAe;IACf,cAAc;IACd,eAAe;IACf,eAAe;AACnB;AACA;IACI,yBAAyB;IACzB,WAAW;AACf;AACA;IACI,WAAW;IACX,YAAY;IACZ,iBAAiB;;AAErB;AACA;IACI,gBAAgB;IAChB,aAAa;IACb,mBAAmB;AACvB;AACA;IACI,kBAAkB;IAClB,aAAa;AACjB;AACA;IACI,WAAW;IACX,YAAY;IACZ,kBAAkB;AACtB;AACA;IACI,yBAAyB;IACzB,YAAY;IACZ,iBAAiB;IACjB,4BAA4B;IAC5B,gBAAgB;AACpB;AACA;IACI,kBAAkB;IAClB,eAAe;IACf,gBAAgB;IAChB,iBAAiB;IACjB,yBAAyB;IACzB,YAAY;;AAEhB;AACA;IACI,WAAW;IACX,YAAY;AAChB;AACA;IACI,aAAa;IACb,WAAW;IACX,eAAe;;AAEnB;AACA;IACI,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,UAAU;IACV,iBAAiB;IACjB,kBAAkB;IAClB,kBAAkB;IAClB,yBAAyB;IACzB,qBAAqB;IACrB,eAAe;IACf,qBAAqB;IACrB,yBAAyB;AAC7B;AACA;IACI,YAAY;IACZ,cAAc;AAClB;AACA;IACI,iBAAiB;IACjB,cAAc;IACd,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;IACd,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,YAAY;IACZ,cAAc;IACd,WAAW;AACf;AACA;IACI,mBAAmB;IACnB,gBAAgB;IAChB,uBAAuB;IACvB,gBAAgB;IAChB,iBAAiB,EAAE,2BAA2B;IAC9C,gBAAgB,EAAE,6CAA6C;AACnE;AACA;IACI,yBAAyB;IACzB,kBAAkB;IAClB,iBAAiB;AACrB;AACA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,oBAAoB;IACpB,kBAAkB;AACtB;AACA;IACI,WAAW;IACX,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,kBAAkB;IAClB,oBAAoB;AACxB","sourcesContent":["*, *::before, *::after {\r\n    margin: 0;\r\n    padding: 0;\r\n    box-sizing: border-box;\r\n    font-family: \"Poppins\", sans-serif;\r\n    font-size: 16px;\r\n\r\n}\r\n:root {\r\n    --font-color: #fafafa;\r\n    --green-color: #0bf25c;\r\n}\r\nbody {\r\n    color: var(--font-color)\r\n}\r\n\r\n.container {\r\n    width: 100%;\r\n    max-width: 100%;