import { StorageManager } from "./taskStorage.js";
import deletePhoto from "./Assets/delete-icon.png";
import completedIcon from "./Assets/checked.png";
import incompleteIcon from "./Assets/unchecked.png";

export const DisplayTask = (() => {
    // get the container that holds all tasks.
    const tasksDiv = document.querySelector('.tasks-div');
    // Clear existing tasks from the DOM
    const clearTasks = () => {
        tasksDiv.innerHTML = '';
    };

    const renderAllTasks = (filterType) => {
        // get tasks from StorageManager module.
        const tasks = StorageManager.filterTasks(filterType);
        clearTasks();
        tasks.forEach((task) => {
            // create elements for all the fields
            const contDiv = document.createElement('div');
            contDiv.className = 'cont-div';
             // Add "Completed" status (checkbox or button)
            const completeIcon = document.createElement('img');
            completeIcon.src = task.completed ? completedIcon : incompleteIcon;
            completeIcon.classList.add('complete-icon');
            //completeIcon.style.backgroundColor = task.completed ? '#0bf25c' : ''; // Optional styling
            
            const titleText = document.createElement('h4');
            const descriptionText = document.createElement('p');
            const dateText = document.createElement('p');
            const importantText = document.createElement('p');
            const deleteIcon = document.createElement('img');

            titleText.textContent = task.title;
            descriptionText.textContent = task.description;
            dateText.textContent = task.date;
            importantText.textContent = task.important ? "Important" : "";

            // Add 'important' class if the task is important
            if (task.important) {
                importantText.classList.add('important');
            }
            

            completeIcon.addEventListener('click', () => {
                task.completed = !task.completed; // Toggle completion status
                StorageManager.updateTask(task); // Update in localStorage
                renderAllTasks('all'); // Re-render tasks
            });

            // Create delete icon
            deleteIcon.src = deletePhoto;  // Adjust the path to your icon
            deleteIcon.alt = 'Delete Task';
            deleteIcon.classList.add('delete-icon');

             // Attach delete event
            deleteIcon.addEventListener('click', () => {
                StorageManager.deleteTask(task.id);
                renderAllTasks(filterType); // Re-render tasks
            });
            // add elements to the container
            contDiv.append(completeIcon, titleText, descriptionText, dateText, importantText, deleteIcon);
            tasksDiv.appendChild(contDiv);
        });
    };

    return {
        renderAllTasks,
    }
})();